class TerminalExpeditor extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      dataList: []
    };
  }

 

  getQuaterList(cityname) {
    var inter = this.state.dataList.filter(elt => elt.nom === cityname);
    if (inter.length === 0) {
      return [];
    } else {
      return inter[0].quartiers;
    }
  }

  getSectorsList(quatername, cityname) {
    var inter1 = this.getQuaterList(cityname);
    if (inter1.length === 0) {
      return [];
    }
    var inter2 = inter1.filter(elt => elt.nom === quatername);

    if (inter2.length === 0) {
      return [];
    } else {
      return inter2[0].secteurs;
    }
  }

  componentDidMount() {
    axios.get(route+"rest/terminal/all")
    .then((res)=>{
      this.setState({ dataList: res.data });
    })
    .catch((err)=>{
      if(err.response){
       switch (err.response.status) {
         case 403:{
             message.error("Erreur!!");
         }        
         default:{
         message.error("Erreurr");
         }
       }
      }
    })
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form {...formItemLayout}>
      <div><h3 className = "tittle"> Terminal Expediteur</h3></div>
        <Form.Item label="ville">
          {getFieldDecorator("te_city", {
            rules: [
              {
                required: true,
                message: "Entrez la ville du terminal d'expedition !"
              }
            ]
          })(
            <Select
              showSearch
              placeholder="choisir une ville"
              optionFilterProp="children"
              style={{ width: "100%" }}
              filterOption={(input, option) =>
                option.props.children
                  .toLowerCase()
                  .indexOf(input.toLowerCase()) >= 0
              }
            >
              {this.state.dataList.map((elt, index) => (
                <Select.Option value={elt.nom} key={index}>
                  {elt.nom}
                </Select.Option>
              ))}
            </Select>
          )}
        </Form.Item>

        <Form.Item label="quatier">
          {getFieldDecorator("te_quarter", {
            rules: [
              {
                required: true,
                message: "Entrez le quatier du terminal d'expedition !"
              }
            ]
          })(
            <Select
              showSearch
              placeholder="choisir un quatier"
              optionFilterProp="children"
              style={{ width: "100%" }}
              filterOption={(input, option) =>
                option.props.children
                  .toLowerCase()
                  .indexOf(input.toLowerCase()) >= 0
              }
            >
              {this.getQuaterList(
                this.props.form.getFieldValue("te_city")
              ).map((elt, index) => (
                <Select.Option value={elt.nom} key={index}>
                  {elt.nom}
                </Select.Option>
              ))}
            </Select>
          )}
        </Form.Item>

        <Form.Item label="Secteur">
          {getFieldDecorator("te_sector", {
            rules: [
              {
                required: true,
                message: "Entrez le secteur du terminal d'expedition !"
              }
            ]
          })(
            <Select
              showSearch
              placeholder="choisir un secteur"
              optionFilterProp="children"
              style={{ width: "100%" }}
              filterOption={(input, option) =>
                option.props.children
                  .toLowerCase()
                  .indexOf(input.toLowerCase()) >= 0
              }
            >
              {this.getSectorsList(
                this.props.form.getFieldValue("te_quarter"),
                this.props.form.getFieldValue("te_city")
              ).map((elt, index) => (
                <Select.Option value={elt} key={index}>
                  {elt}
                </Select.Option>
              ))}
            </Select>
          )}
        </Form.Item>
      </Form>
    );
  }
}

const TerminalExpeditorForm = Form.create({ name: "terminalExpedition" })(
  TerminalExpeditor
);

export default TerminalExpeditorForm;
