import React from "react";
import { InputNumber, Form ,Select,message} from "antd";
import formItemLayout from "../constants/formwrappercss";
import axios from 'axios';
import route from '../constants/settings';


const VirtualtransporteurList = [
  {
    id: 1,
    name: "loge",
    identifiant: "me",
    published: true,
    description: "jj"
  },

  {
    id: 2,
    name: "logie",
    identifiant: "me",
    published: true,
    description: "jje"
  }
];

class Dimension extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      transporteurList: []
    };
  }
  componentDidMount() {
    axios.get(route+"rest/transporteur/allSimple")
    .then((res)=>{
      this.setState({ transporteurList: res.data });
    })
    .catch((err)=>{
       switch (err.response.status) {
         case 403:{
             message.error("Erreur!!");
         }        
         default:{
         message.error("Erreurr");
         }
       }
    })
  }
  isValid = () => {
    var val = false;
    var value = {};
    this.props.form.validateFields((errors, values) => {
      if (!errors) {
        val = true;
        value = values;
      }
    });

    return [val, value];
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form {...formItemLayout}>
       <div className = "tittle"><h3 className = "tittle">Dimension de Colis</h3></div>
        <Form.Item label="Largeur(cm)">
          {getFieldDecorator("width", {
            rules: [
              {
                required: true,
                message: "Entrez la largeur du colis!"
              }
            ]
          })(<InputNumber />)}
        </Form.Item>

        <Form.Item label="Longeur(cm)">
          {getFieldDecorator("length", {
            rules: [
              {
                required: true,
                message: "Entrez la longeur du colis!"
              }
            ]
          })(<InputNumber />)}
        </Form.Item>

        <Form.Item label="Hauteur(cm)">
          {getFieldDecorator("height", {
            rules: [
              {
                required: true,
                message: "Entrez la hauteur du colis!"
              }
            ]
          })(<InputNumber />)}
        </Form.Item>

        <Form.Item label="Poids(kg)">
          {getFieldDecorator("weight", {
            rules: [
              {
                required: true,
                message: "Entrez le poids du colis!"
              }
            ]
          })(<InputNumber />)}
        </Form.Item>
        <Form.Item label="Nom Du transporteur">
          {getFieldDecorator("transporter", {
            rules: [
              {
                required: true,
                message: "choisissez un transporteur !"
              }
            ]
          })(
            <Select
              showSearch
              placeholder="choisir un transporteur"
              optionFilterProp="children"
              style={{ width: "100%" }}
              filterOption={(input, option) =>
                option.props.children
                  .toLowerCase()
                  .indexOf(input.toLowerCase()) >= 0
              }
            >
              {this.state.transporteurList.map((elt, index) => (
                <Select.Option key={index} value={elt}>
                  {elt}
                </Select.Option>
              ))}
            </Select>
          )}
        </Form.Item>
      </Form>
    );
  }
}

const DimensionForm = Form.create({ name: "dimension" })(Dimension);

export default DimensionForm;
