import React from "react";
import { Modal } from "antd";
import Payement from './Payement'

class Info extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: this.props.visible,
      list:this.props.list,
      ok:false,
    };
  }
  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false,
      ok:true,
    });
  };

  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,
      ok:true
    });
  };

  componentWillReceiveProps(nextProps) {
    this.setState({ visible: nextProps.visible,list:nextProps.list });
  }

  render() {
    return (
      <div>
        <Modal
          title="Basic Modal"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
        >
        {this.state.list.map((elt,index)=>(
          <p key ={index}> {elt} </p>
        ))}
        </Modal>
        {
          this.state.ok &&
          <Payement visible={this.state.ok}/>
        }
        
      </div>
    );
  }
}

export default Info;
