import React, { Component } from 'react'
import '../App.css';
import {Link} from 'react-router-dom'
export class Home extends Component {
  render() {
    return (
      <div>
        <div id="nbar">
        </div>
        <div id="hometext">
        <h2>Bienvenu sur nos pages de réservation d’espace casier des terminaux à colis et le suivi des colis </h2>
        </div>
        <div className="row" id = "buttons">
            <div className="col-md-6">
                <Link to = "/prereservation" className="btn" id="leftbtn">
                        Reservation <span className="badge badge-primary btn-lg"></span>
                </Link>
            </div>
            <div className="col-md-6 ">
               <button className="btn" id="rightbtn">
                        Suivie <span className="badge badge-primary btn-lg"></span>
                </button>
            </div>
        </div>
      </div>
    )
  }
}

export default Home
