import React from "react";
import { Form, Select, Input, Modal,message} from "antd";
import axios from 'axios';
import route from '../constants/settings';
import {Redirect} from 'react-router-dom';

const CollectionCreateForm = Form.create({ name: "final-form" })(
  // eslint-disable-next-line
  class extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        payementList:[],
      };
    }
    
    componentWillReceiveProps(nextProps) {
      this.setState({ payementList:nextProps.payementList});
    }
    render() {
      const { visible, onCancel, onCreate, form } = this.props;
      const { getFieldDecorator } = form;
      return (
        <Modal
          visible={visible}
          title="Informations Finales"
          okText="Sommettre"
          onCancel={onCancel}
          onOk={onCreate}
        >
          <Form layout="vertical">
            <Form.Item label="Mode de Payement">
              {getFieldDecorator("payment", {
                rules: [
                  {
                    required: true,
                    message: "Entrez le pays de l'expediteur !"
                  }
                ]
              })(
                <Select
                  showSearch
                  placeholder="choisir un mode de payement"
                  optionFilterProp="children"
                  style={{ width: "100%" }}
                  filterOption={(input, option) =>
                    option.props.children
                      .toLowerCase()
                      .indexOf(input.toLowerCase()) >= 0
                  }
                >
                  {this.state.payementList.map((elt, index) => (
                    <Select.Option value={elt} key={index}>
                      {elt}
                    </Select.Option>
                  ))}
                </Select>
              )}
            </Form.Item>

            <Form.Item label="Numero de telephone">
              {getFieldDecorator("phone", {
                rules: [
                  {
                    required: true,
                    message: "Entrez le numero !"
                  }
                ]
              })(<Input />)}
            </Form.Item>
          </Form>
        </Modal>
      );
    }
  }
);

class Payement extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      payementList:[],
      visible: this.props.visible,
      redirect:false,
    };
  }

  componentWillReceiveProps(nextProps) {
    this.setState({ visible: nextProps.visible});
  }
  componentDidMount() {
    axios.get(route+"rest/modepaiement/allSimple")
    .then((res)=>{
      this.setState({ payementList: res.data });
    })
    .catch((err)=>{
      if(err.respons){
        switch (err.response.status) {
          case 404:{
              message.error("Erreur de Validation");
          }
          case 500:{
           message.error("Erreur cote serveur");
         }         
          default:{
          message.error("Erreurr");
          }
        }
      }
    })
  }

  handleCancel = () => {
    this.setState({ visible: false });
  };

  handleCreate = () => {
    const form = this.formRef.props.form;
    form.validateFields((err, values) => {
      if (err) {

        return;
      }

      console.log("Received values of form: ", values);
    axios.post(route+"rest/paiementreservation/",values)
    .then((res)=>{
      this.setState({visible:false,redirect:true})
      form.resetFields();
      message.info("Soumission reussie")
    })
    .catch((err)=>{
      if(err.respons){
        switch (err.response.status) {
          case 404:{
              message.error("Erreur de Validation");
          }
          case 500:{
           message.error("Erreur cote serveur");
         }         
          default:{
          message.error("Erreurr");
          }
        }
      }
    })

    });
  };

  saveFormRef = formRef => {
    this.formRef = formRef;
  };

  render() {
    return (
      <div>
        <CollectionCreateForm
          wrappedComponentRef={this.saveFormRef}
          visible={this.state.visible}
          onCancel={this.handleCancel}
          onCreate={this.handleCreate}
          payementList={this.state.payementList}
        />
        {this.state.redirect &&
         <Redirect to ="./prereservation"/>
        }
      </div>
    );
  }
}

const PayementForm = Form.create({ name: "register" })(Payement);
export default PayementForm;
