import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import '../App.css';
export class Home extends Component {
  render() {
    return (
      <div>
        <div id="nbar">
        </div>
        <div id="hometext">
        <h2>Le Formulaire suivant contient les informations nécessaires à la réservation d’un colis d’un casier. Veuillez remplir l’ensemble du formulaire. La validation de la réservation d’un espace casier pour l’envoi de colis à un coût qui s’élève d’une valeur de la somme de X. Les frais d’expédition seront payés par les différents type de mobile money </h2>
        </div>
        <div class="row" id = "buttons">
            <div class="col-md-3">
                
            </div>
            <div class="col-md-6 ">
               <Link to= "/reservation" class="btn" id="reserv">
                    Remplir le Formulaire<span class="badge badge-primary btn-lg"></span>
                </Link>
            </div>
            <div class="col-md-3">
                
            </div>
        </div>
      </div>
    )
  }
}

export default Home
